import React from 'react';
import {
    ResponsiveContainer,
    BarChart,
    Bar,
    XAxis,
    YAxis,
    CartesianGrid,
    Tooltip,
    Legend,
    ComposedChart,
    Line,
} from 'recharts';
import { type TrendData } from '@/lib/reportUtils';

type Props = {
    data: TrendData[];
};

export function TrendAnalysis({ data }: Props) {
    if (data.length === 0) {
        return (
            <div className="p-8 text-center text-slate-500 bg-white rounded-lg shadow-sm border border-slate-100">
                <p>„Éá„Éº„Çø„Åå„ÅÇ„Çä„Åæ„Åõ„Çì</p>
            </div>
        );
    }

    return (
        <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
            {/* 1. Weekly Focus Time */}
            <div className="bg-white p-6 rounded-lg shadow-sm border border-slate-100">
                <h3 className="text-lg font-bold text-slate-800 mb-6 flex items-center gap-2">
                    <span>‚è±Ô∏è</span> ÈÄ±Èñì„Éï„Ç©„Éº„Ç´„ÇπÊôÇÈñì
                </h3>
                <div className="h-64 w-full">
                    <ResponsiveContainer width="100%" height="100%">
                        <BarChart data={data} margin={{ top: 10, right: 10, left: 0, bottom: 0 }}>
                            <CartesianGrid strokeDasharray="3 3" vertical={false} />
                            <XAxis dataKey="name" tick={{ fontSize: 12 }} />
                            <YAxis tick={{ fontSize: 12 }} unit="h" />
                            <Tooltip
                                contentStyle={{ borderRadius: '8px', border: 'none', boxShadow: '0 4px 6px -1px rgb(0 0 0 / 0.1)' }}
                                cursor={{ fill: '#f1f5f9' }}
                            />
                            <Legend />
                            <Bar
                                dataKey="totalActualHours"
                                name="ÂÆüÁ®ºÂÉç(ÊôÇÈñì)"
                                fill="#6366f1"
                                radius={[4, 4, 0, 0]}
                                barSize={40}
                            />
                        </BarChart>
                    </ResponsiveContainer>
                </div>
            </div>

            {/* 2. Task Volume & Completion Rate */}
            <div className="bg-white p-6 rounded-lg shadow-sm border border-slate-100">
                <h3 className="text-lg font-bold text-slate-800 mb-6 flex items-center gap-2">
                    <span>üìä</span> „Çø„Çπ„ÇØÊ∂àÂåñÁéá
                </h3>
                <div className="h-64 w-full">
                    <ResponsiveContainer width="100%" height="100%">
                        <ComposedChart data={data} margin={{ top: 10, right: 10, left: 0, bottom: 0 }}>
                            <CartesianGrid strokeDasharray="3 3" vertical={false} />
                            <XAxis dataKey="name" tick={{ fontSize: 12 }} />
                            <YAxis yAxisId="left" tick={{ fontSize: 12 }} />
                            <YAxis yAxisId="right" orientation="right" tick={{ fontSize: 12 }} unit="%" domain={[0, 100]} />
                            <Tooltip
                                contentStyle={{ borderRadius: '8px', border: 'none', boxShadow: '0 4px 6px -1px rgb(0 0 0 / 0.1)' }}
                            />
                            <Legend />
                            <Bar
                                yAxisId="left"
                                dataKey="totalTasks"
                                name="„Çø„Çπ„ÇØÁ∑èÊï∞"
                                fill="#e2e8f0"
                                radius={[4, 4, 0, 0]}
                                barSize={30}
                            />
                            <Line
                                yAxisId="right"
                                type="monotone"
                                dataKey="completionRate"
                                name="ÂÆå‰∫ÜÁéá(%)"
                                stroke="#10b981"
                                strokeWidth={2}
                                dot={{ r: 4, fill: '#10b981', strokeWidth: 2, stroke: '#fff' }}
                            />
                        </ComposedChart>
                    </ResponsiveContainer>
                </div>
            </div>
        </div>
    );
}
